.Paint img {
    width: 100%;
    height: auto;
    display: block;
}
.PaintTitle{
    font-size: 5vw;
    
}
.PaintAuthor{
    font-size: 4vw;
   
}
.Paint {
    position: relative;
    margin-bottom: 1.5rem;
    
}
.About {
    padding: 0.5rem;
    display: flex;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    min-height: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0px;
    left: 0px;
    text-align: center;
    text-decoration: none;
}




@media(min-width:500px) {
    .PaintTitle{
        font-size: 3.5vw;
        
    }
    .PaintAuthor{
        font-size: 3vw;
       
    }
}
@media(min-width:800px) {
    
    .Paint .About {
        display: none;

    }

    .Paint:hover .About {
        display: flex;
    }
    .PaintTitle{
        font-size: 1.3rem;
        
    }
    .PaintAuthor{
        font-size: 1rem;
       
    }
}
@media (min-width:1200px) {
    .PaintTitle{
        font-size: 1rem;
        
    }
    .PaintAuthor{
        font-size: 1rem;
       
    }
}